<template>
  <div id="zoomer-dialog-component">
    <el-dialog
      title="Image Zoomer"
      v-model="visible"
      width="100%"
      height="100%"
      top="0"
    >
      <v-zoomer
        ref="zoomer"
        style="width: 1000px; height: calc(100vh - 170px); border: solid 1px silver; margin: 0 auto;"
      >
        <div
          class="img"
          :style="{ backgroundImage: `url('${url}')` }"
          :draggable="false"
        ></div>
      </v-zoomer>
      <div class="console-wrapper">
        <el-button type="success" size="sm" @click="onReduce">-</el-button>
        <el-button type="success" size="sm" @click="onPlus">+</el-button>
        <el-button type="info" size="sm" @click="onReset">Reset</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref } from "vue";

export default defineComponent({
  name: "ZoomerDialog",
  setup() {
    const zoomer = ref<{ reset: any; zoomIn: any; zoomOut: any } | null>(null);
    const visible = ref(false);
    const url = ref("");

    const open = (imageUrl: string) => {
      visible.value = true;
      url.value = imageUrl;
    };

    const onPlus = () => {
      if (!zoomer.value) return;
      zoomer.value.zoomIn();
    };

    const onReduce = () => {
      if (!zoomer.value) return;
      zoomer.value.zoomOut();
    };

    const onReset = () => {
      if (!zoomer.value) return;
      zoomer.value.reset();
    };

    return {
      zoomer,
      visible,
      url,

      open,
      onPlus,
      onReduce,
      onReset
    };
  }
});
</script>

<style lang="stylus" scoped>
#zoomer-dialog-component
  width 100%
  height 100%
  :deep() .el-dialog
    margin 0 auto

  .img
    width 100%
    height 100%
    background-repeat no-repeat
    background-position center
    background-size contain

  .console-wrapper
    width 1000px
    flex(flex-end)
    margin 15px auto 0
    .el-button
      margin-right 8px
</style>
