<template>
  <div id="youtube-dialog-component">
    <el-dialog
      custom-class="youtube-el-dialog"
      title="Youtube Video"
      v-model="visible"
      width="100%"
      height="100%"
      top="0"
    >
      <div class="youtube-container">
        <div class="youtube-video-container">
          <Youtube
            :src="selectedUrl"
            @ready="onReady"
            ref="youtube"
            width="1300px"
            height="550px"
          ></Youtube>
        </div>

        <el-carousel
          height="150px"
          :autoplay="false"
          arrow="always"
          :style="{ marginTop: '30px', width: '1300px', margin: '0 auto' }"
          indicator-position="none"
        >
          <el-carousel-item
            v-for="(item, index) in getSecondaryMedia(urls)"
            :key="index"
          >
            <div class="carouse-item">
              <div
                v-for="(url, _index) in item"
                :key="_index"
                class="secondary-image"
                :class="{ active: url === selectedUrl }"
                :style="{
                  backgroundImage: `url('${getImageUrl(url)}')`
                }"
                @click="onCarouseItemClick(url)"
              ></div>
            </div>
          </el-carousel-item>
        </el-carousel>
      </div>
    </el-dialog>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref } from "vue";
import Youtube from "vue3-youtube";
import { iYoutube } from "@/typings/component";

const getImageUrl = (url: string) => {
  const matches = url.match(/https:\/\/youtu.be\/(.*[^/])/);
  if (matches && matches[1]) {
    return `https://img.youtube.com/vi/${matches[1]}/hqdefault.jpg`;
  }
  return "";
};

export default defineComponent({
  name: "YoutubeDialog",
  components: { Youtube },
  setup() {
    //@ts-ignore
    const zoomer = ref<{ reset: any; zoomIn: any; zoomOut: any } | null>(null);
    const visible = ref(false);
    const urls = ref<string[]>([]);
    const selectedUrl = ref("");
    const youtube = ref<iYoutube | null>(null);

    const open = (data: string[]) => {
      visible.value = true;
      urls.value = data;
      selectedUrl.value = urls.value[0];
    };

    const onReady = () => {
      if (!youtube.value) return;
      youtube.value.playVideo();
    };

    const getSecondaryMedia = (urls: string[]) => {
      return urls.reduce((result, item: string, index: number) => {
        if (index % 5) {
          (result[result.length - 1] as string[]).push(item);
        } else {
          (result as string[][]).push([item]);
        }
        return result;
      }, []);
    };

    const onCarouseItemClick = (url: string) => {
      selectedUrl.value = url;
    };

    return {
      youtube,
      zoomer,
      visible,
      urls,
      selectedUrl,

      open,
      onReady,
      onCarouseItemClick,
      getSecondaryMedia,
      getImageUrl
    };
  }
});
</script>

<style lang="stylus" scoped>
#youtube-dialog-component
  width 100%
  height 100%

  :deep() .el-dialog
    margin 0 auto

  .youtube-container
    height calc(100vh - 114px)
    margin 0
    .youtube-video-container
      flex()

    .carouse-item
      margin 0
      height 150px
      flex(flex-start)

    .secondary-image
      cursor pointer
      width 20%
      height 120px
      margin 0 1.5%
      background-repeat no-repeat
      background-size contain
      background-position center
    .active
      border 2px solid $color-active

  :deep() .youtube-el-dialog
    min-width 1430px
</style>
