
import { defineComponent, ref, toRefs, computed, Ref, PropType } from "vue";

import Fabric3dDialog from "@/components/Fabric3dDialog.vue";
import ZoomerDialog from "@/components/ZoomerDialog.vue";
import YoutubeDialog from "@/components/YoutubeDialog.vue";

import { iCreateDialog } from "@/typings/component";

export default defineComponent({
  name: "Avatar",
  components: {
    Fabric3dDialog,
    ZoomerDialog,
    YoutubeDialog
  },
  props: {
    image: {
      type: String,
      default: ""
    },
    urls: {
      type: Array as PropType<string[]>,
      default: () => []
    },
    tg3d: {
      type: Object,
      default: null
    }
  },
  setup(props) {
    const fabric3dDialog: Ref<iCreateDialog | null> = ref(null);
    const playDialog: Ref<iCreateDialog | null> = ref(null);
    const zoomDialog: Ref<iCreateDialog | null> = ref(null);
    const { image, urls, tg3d } = toRefs(props);

    // @ts-ignore
    const has3DMode = computed(() => window.TG3D && tg3d.value);

    const onModelClick = () => {
      if (!fabric3dDialog.value || !tg3d.value) return;
      fabric3dDialog.value.open(tg3d.value);
    };
    const onPlayClick = () => {
      if (!playDialog.value) return;
      playDialog.value.open(urls.value);
    };
    const onZoomerClick = () => {
      if (!zoomDialog.value) return;
      zoomDialog.value.open(image.value);
    };

    return {
      has3DMode,
      fabric3dDialog,
      playDialog,
      zoomDialog,

      onModelClick,
      onPlayClick,
      onZoomerClick
    };
  }
});
