
import { defineComponent, ref } from "vue";

export default defineComponent({
  name: "ZoomerDialog",
  setup() {
    const zoomer = ref<{ reset: any; zoomIn: any; zoomOut: any } | null>(null);
    const visible = ref(false);
    const url = ref("");

    const open = (imageUrl: string) => {
      visible.value = true;
      url.value = imageUrl;
    };

    const onPlus = () => {
      if (!zoomer.value) return;
      zoomer.value.zoomIn();
    };

    const onReduce = () => {
      if (!zoomer.value) return;
      zoomer.value.zoomOut();
    };

    const onReset = () => {
      if (!zoomer.value) return;
      zoomer.value.reset();
    };

    return {
      zoomer,
      visible,
      url,

      open,
      onPlus,
      onReduce,
      onReset
    };
  }
});
