<template>
  <div v-if="image" class="fabric-image-with-3d-mode-component">
    <div class="img" :style="{ backgroundImage: `url('${image}')` }"></div>

    <div class="console-container">
      <div v-if="urls.length" class="console-item" @click="onPlayClick">
        <font-awesome-icon
          class="icon"
          :icon="['fas', 'play']"
        ></font-awesome-icon>
      </div>
      <div v-if="has3DMode" class="console-item" @click="onModelClick">
        <p>3D</p>
      </div>
      <div class="console-item" @click="onZoomerClick">
        <font-awesome-icon
          class="icon"
          :icon="['fas', 'search']"
        ></font-awesome-icon>
      </div>
    </div>

    <Fabric3dDialog ref="fabric3dDialog"></Fabric3dDialog>
    <YoutubeDialog ref="playDialog"></YoutubeDialog>
    <ZoomerDialog ref="zoomDialog"></ZoomerDialog>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, toRefs, computed, Ref, PropType } from "vue";

import Fabric3dDialog from "@/components/Fabric3dDialog.vue";
import ZoomerDialog from "@/components/ZoomerDialog.vue";
import YoutubeDialog from "@/components/YoutubeDialog.vue";

import { iCreateDialog } from "@/typings/component";

export default defineComponent({
  name: "Avatar",
  components: {
    Fabric3dDialog,
    ZoomerDialog,
    YoutubeDialog
  },
  props: {
    image: {
      type: String,
      default: ""
    },
    urls: {
      type: Array as PropType<string[]>,
      default: () => []
    },
    tg3d: {
      type: Object,
      default: null
    }
  },
  setup(props) {
    const fabric3dDialog: Ref<iCreateDialog | null> = ref(null);
    const playDialog: Ref<iCreateDialog | null> = ref(null);
    const zoomDialog: Ref<iCreateDialog | null> = ref(null);
    const { image, urls, tg3d } = toRefs(props);

    // @ts-ignore
    const has3DMode = computed(() => window.TG3D && tg3d.value);

    const onModelClick = () => {
      if (!fabric3dDialog.value || !tg3d.value) return;
      fabric3dDialog.value.open(tg3d.value);
    };
    const onPlayClick = () => {
      if (!playDialog.value) return;
      playDialog.value.open(urls.value);
    };
    const onZoomerClick = () => {
      if (!zoomDialog.value) return;
      zoomDialog.value.open(image.value);
    };

    return {
      has3DMode,
      fabric3dDialog,
      playDialog,
      zoomDialog,

      onModelClick,
      onPlayClick,
      onZoomerClick
    };
  }
});
</script>

