
import { defineComponent, ref, nextTick, watch } from "vue";
import * as _ from "ramda";
import { iCreateDialog } from "@/typings/component";
import { formatImageUrl, loading } from "@/utils";
import { modeList } from "@/constants/3d";
import SwitchIcon from "@/assets/images/icons/png/background-color-switch.png";

export default defineComponent({
  name: "Fabric3dDialog",
  setup() {
    const visible = ref(false);
    const selectedId = ref(0);
    const fabricScan = ref<any>(null);
    const fabric3DContainer = ref<iCreateDialog | null>(null);
    const garmentViewer = ref<any>(null);
    const backgroundColor = ref("white");

    const init = async () => {
      if (!visible.value || !fabricScan.value) return;

      await nextTick();
      if (!garmentViewer.value) {
        // @ts-ignore
        garmentViewer.value = new window.TG3D.stylebook.Viewer({
          ele: fabric3DContainer.value
        });
      }

      const loadingInstance = loading();
      garmentViewer.value.clear3DModels();
      garmentViewer.value
        .showGarmentById(modeList[selectedId.value].key)
        .then(() => {
          garmentViewer.value.changeMaterialToParts(
            modeList[selectedId.value].render_parts,
            fabricScan.value,
            () => {
              loadingInstance.close();
            }
          );

          // @ts-ignore
          window.TG3D.api.fabrics.get(fabricScan.value.id).then((data: any) => {
            console.log('fetch data: ', data);
            console.log("raw data: ", fabricScan.value);
          });
        });
    };

    watch([fabricScan, selectedId], init);

    const open = (scan: any) => {
      fabricScan.value = scan;
      visible.value = true;
    };

    const onClose = () => {
      fabricScan.value = null;
    };

    const onCarouseItemClick = (id: number) => {
      selectedId.value = id;
    };

    const onSwitchClick = () => {
      backgroundColor.value =
        backgroundColor.value === "white" ? "#919191" : "white";
    };

    return {
      visible,
      fabric3DContainer,
      modeList,
      selectedId,
      backgroundColor,

      onCarouseItemClick,

      open,
      onClose,
      onSwitchClick,

      formatImageUrl,
      SwitchIcon,
      _
    };
  }
});
